import { Component , ElementRef } from '@angular/core';
import { FetchDataService } from './fetch-data.service';
import { FileUploadService } from './file-upload.service';
import { HttpErrorResponse } from '@angular/common/http';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'first angular 5 webpage';
  result:any;
  show = 1;
  price = 10000;
  dt = new Date(); 
  obj = { foo: 'bar', baz: 'qux', nested: {xyz: 3, numbers: [1, 2, 3, 4, 5] } };

  constructor(private _fetchData: FetchDataService , private el: ElementRef, 
    private fileToUpload: FileUploadService)
    {
    this.result = _fetchData.data;
    }
  sendData(){
    this._fetchData.sendData();
  }
   
  uploadFile(){
    const frmData = new FormData(this.el.nativeElement.querySelector('form'));
    this.fileToUpload.uploadFile(frmData)
    .subscribe(data => console.log(data), (err: HttpErrorResponse) => {
      if(err.error instanceof ErrorEvent){
        console.error('An error occurred:', err.error.message);
      }
      else{
        console.error(
          `Backend returned code ${err.status}, ` +
          `body was: ${err.error.text}`)
      }
    });
  }

}
